body {
  margin: 0;
}

#container {
  display: flex;
  gap: 100px;
  height: 100vh;
}

#formContainer {
  padding: 50px;
  height: 100;
  background-color: lightgrey;
}

h2 {
  margin-top: 30px;
  margin-bottom: 10px;
}

button {
  margin-top: 10px;
}

form {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 5px;
}
